import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Header from "../../components/header/Header";
import SideBar from "../../components/sidebar/SideBar";
import AudioIn from "../../images/volume.png";
import AudioMute from "../../images/mute.png";
import firebase from "firebase";
import { firestore } from "../../config/db";

import Axios from "axios";
import MyBooksTable from "./mybooksTable";
import PaginationLink from "./pagination";
import { useParams } from "react-router";
import axios from "axios";
import { Button } from "@material-ui/core";
import SimpleAlerts from "../../components/alert/Alert";
import UploadImage from "../../images/upload.png";
import PdfFile from "../../images/pdf (1).png";
import emptyFile from "../../images/file-rounded-empty-sheet.png";
import Player from "./audioPlayer";
import { Link, useHistory } from "react-router-dom";
import ViewPdf from "./viewBook";
import DeleteAlert from "./deleteAlert";
import PageLoader from "../../components/pageLoader/loader";
import UploadAudioComponent from "./uploadAudioComponent";
import EpubPng from "../../images/epub.png";
import MenuIcon from "@material-ui/icons/Menu";
export default function UpdateBookCover() {
  const userReducer = useSelector((state) => state.userReducer);
  const userData = userReducer.credentials;
  const history = useHistory();
  const dispatch = useDispatch();
  const [authExpire, setauthExpire] = useState(false);
  const getAuthChecker = sessionStorage.getItem("userAuth");
  if (getAuthChecker === null || getAuthChecker === false) {
    window.location.href = "/signin";
  }
  const getTokens = JSON.parse(localStorage.getItem("persist:reducerStore"));
  const tokens = JSON.parse(getTokens.userReducer);
  const actualToken = tokens.credentials.token;

  const userId = tokens.credentials.message._id;
  const [uploadImage, setuploadImage] = useState({
    published_image: "",
    loader: false,
  });
  const productId = useParams().productId;
  const [fieldDetails, setfieldDetails] = useState({
    title: "",
    author: "",
    image: "",
    audio: "",
    richAudio: "",
    bookFile: "",
    description: "",
    category: "",
    price: "",
    language: "",
    isbnNumber: "",
  });

  const [getCategories, setgetCategories] = useState([]);
  const [messageDetails, setmessageDetails] = useState({
    message: "",
    status: "",
    loader: false,
  });
  const [updateLoader, setupdateLoader] = useState(false);
  useEffect(() => {
    setupdateLoader(true);
    axios.get(`${process.env.REACT_APP_URL}/category`).then((data) => {
      if (data.data.success) {
        setgetCategories(data.data.message);
      } else {
        setgetCategories([]);
      }
    });

    const requestOptions = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",

        Authorization: `Bearer ${actualToken}`,
      },
    };
    fetch(
      `${process.env.REACT_APP_URL}/publish/book/publisher/${productId}`,
      requestOptions
    ).then((response) =>
      response.json().then((data) => {
        setupdateLoader(false);
        if (data.success) {
          setfieldDetails({
            title: data.message.title,
            author: data.message.author,
            category: data.message.category._id,
            description: data.message.description,
            language: data.message.language,
            price: data.message.price,
            audio: data.message.audio,
            richAudio: data.message.richAudio,
            image: data.message.image,
            bookFile: data.message.bookFile,
            isbnNumber: data.message.isbnNumber,
          });
        } else {
          if (data.message === "jwt expired") {
            setauthExpire(true);
            sessionStorage.setItem(false);
          }
        }
      })
    );

    // in your case set state to returned token
  }, []);
  const handleUpdateDetails = (value, attr) => {
    setfieldDetails({
      ...fieldDetails,
      [attr]: value,
    });
  };
  const handleUpdateButton = () => {
    setmessageDetails({
      ...messageDetails,
      loader: true,
    });
    const requestOptions = {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${actualToken}`,
      },
      body: JSON.stringify({
        title: fieldDetails.title,
        author: fieldDetails.author,
        image: fieldDetails.image,
        audio: fieldDetails.audio,
        richAudio: fieldDetails.richAudio,
        bookFile: fieldDetails.bookFile,
        description: fieldDetails.description,
        category: fieldDetails.category,
        price: fieldDetails.price,
        language: fieldDetails.language,
        isbnNumber: fieldDetails.isbnNumber,
      }),
    };
    fetch(
      `${process.env.REACT_APP_URL}/publish/book/publisher/update/${productId}`,
      requestOptions
    )
      .then((data) => {
        data.json().then((result) => {
          if (result.success) {
            setfieldDetails({
              title: result.message.title,
              author: result.message.author,
              category: result.message.category.name,
              description: result.message.description,
              language: result.message.language,
              price: result.message.price,
              audio: result.message.audio,
              richAudio: result.message.richAudio,
              image: result.message.image,
              bookFile: result.message.bookFile,
              isbnNumber: result.message.isbnNumber,
            });
            setmessageDetails({
              message: "update successful",
              status: "success",
              loader: false,
            });
          } else {
            setmessageDetails({
              message: result.message,
              status: "error",
              loader: false,
            });
          }
        });
      })
      .catch((error) => {
        setmessageDetails({
          message: error.message,
          status: "error",
          loader: false,
        });
      });
  };
  const [fileUpdateDetails, setfileUpdateDetails] = useState({
    imageUpload: "",
    audioUpload: "",
    richAudioUpload: "",
    bookFileUpload: "",
    loader1: false,
    loader2: false,
    loder3: false,
    loader4: false,
  });
  const handleImageUpload = (value, attr) => {
    setfileUpdateDetails({
      ...fileUpdateDetails,
      [attr]: value,
    });
  };

  const handleUploadImageButton = () => {
    setfileUpdateDetails({
      ...fileUpdateDetails,
      loader1: true,
    });
    const formData = new FormData();

    formData.append("file", fileUpdateDetails.imageUpload);
    const requestOptions = {
      headers: {
        Authorization: `Bearer ${actualToken}`,
      },
    };

    axios
      .put(
        `${process.env.REACT_APP_URL}/publish/image/${productId}`,
        formData,
        requestOptions
      )
      .then((data) => {
        setfileUpdateDetails({
          ...fileUpdateDetails,
          loader1: false,
        });

        if (data.data.success) {
          console.log(data.data.message);
          setfileUpdateDetails({
            ...fileUpdateDetails,
            imageUpload: "",
          });
          setfieldDetails({
            title: data.data.message.title,
            author: data.data.message.author,
            image: data.data.message.image,
            audio: data.data.message.audio,
            richAudio: data.data.message.richAudio,
            bookFile: data.data.message.bookFile,
            description: data.data.message.description,
            category: data.data.message.category,
            price: data.data.message.price,
            language: data.data.message.language,
            isbnNumber: data.data.message.isbnNumber,
          });
          // console.log(data.data.message);
        } else {
          setmessageDetails({
            ...messageDetails,
            message: data.data.message,
            status: "error",
            loader1: false,
          });
          console.log(data.data.message);
        }
      })
      .catch((error) => {
        setfileUpdateDetails({
          ...fileUpdateDetails,
          loader1: false,
        });
        setmessageDetails({
          ...messageDetails,
          message: error.message,
          status: "error",
          loader1: false,
        });
      });
  };
  const handleShortAudioUpload = (value, attr) => {
    const fileName = value.name;
    const fileExtension = fileName.split(".").pop();
    if (fileExtension === "mp3") {
      setfileUpdateDetails({
        ...fileUpdateDetails,
        [attr]: value,
      });
    } else {
      setfileUpdateDetails({
        ...fileUpdateDetails,
        audioUpload: "",
      });
      alert("the file you upload is not supported");
    }
  };
  const handleUploadShortAudioButton = () => {
    setfileUpdateDetails({
      ...fileUpdateDetails,
      loader2: true,
    });
    const formData = new FormData();

    formData.append("file", fileUpdateDetails.audioUpload);
    const requestOptions = {
      headers: {
        Authorization: `Bearer ${actualToken}`,
      },
    };

    axios
      .put(
        `${process.env.REACT_APP_URL}/publish/${productId}`,
        formData,
        requestOptions
      )
      .then((data) => {
        setfileUpdateDetails({
          ...fileUpdateDetails,
          loader2: false,
        });

        if (data.data.success) {
          console.log(data.data.message);
          setfileUpdateDetails({
            ...fileUpdateDetails,
            audioUpload: "",
          });
          setfieldDetails({
            title: data.data.message.title,
            author: data.data.message.author,
            image: data.data.message.image,
            audio: data.data.message.audio,
            richAudio: data.data.message.richAudio,
            bookFile: data.data.message.bookFile,
            description: data.data.message.description,
            category: data.data.message.category,
            price: data.data.message.price,
            language: data.data.message.language,
            isbnNumber: data.data.message.isbnNumber,
          });
          // console.log(data.data.message);
        } else {
          setmessageDetails({
            ...messageDetails,
            message: data.data.message,
            status: "error",
            loader2: false,
          });
          console.log(data.data.message);
        }
      })
      .catch((error) => {
        setfileUpdateDetails({
          ...fileUpdateDetails,
          loader2: false,
        });
        setmessageDetails({
          ...messageDetails,
          message: error.message,
          status: "error",
          loader2: false,
        });
      });
  };
  const handleRichAudioUpload = (value, attr) => {
    const fileName = value.name;
    const fileExtension = fileName.split(".").pop();
    if (fileExtension === "mp3") {
      setfileUpdateDetails({
        ...fileUpdateDetails,
        [attr]: value,
      });
    } else {
      setfileUpdateDetails({
        ...fileUpdateDetails,
        richAudioUpload: "",
      });
      alert("the file you upload is not supported");
    }
  };
  console.log({ productId: productId });
  const handlePdfoUpload = (value, attr) => {
    const fileName = value.name;
    const fileExtension = fileName.split(".").pop();
    // setfileUpdateDetails({
    //   ...fileUpdateDetails,
    //   [attr]: value,
    // });
    if (fileExtension === "epub") {
      setfileUpdateDetails({
        ...fileUpdateDetails,
        [attr]: value,
      });
    } else {
      setfileUpdateDetails({
        ...fileUpdateDetails,
        bookFileUpload: "",
      });
      alert("the file you upload is not supported");
    }
  };
  const handleUploadPdfButton = () => {
    setfileUpdateDetails({
      ...fileUpdateDetails,
      loader4: true,
    });
    const formData = new FormData();

    formData.append("file", fileUpdateDetails.bookFileUpload);
    const requestOptions = {
      headers: {
        Authorization: `Bearer ${actualToken}`,
      },
    };

    axios
      .put(
        `${process.env.REACT_APP_URL}/publish/pdf/${productId}`,
        formData,
        requestOptions
      )
      .then((data) => {
        setfileUpdateDetails({
          ...fileUpdateDetails,
          loader4: false,
        });

        if (data.data.success) {
          console.log(data.data.message);
          setfileUpdateDetails({
            ...fileUpdateDetails,
            bookFileUpload: "",
          });
          setfieldDetails({
            title: data.data.message.title,
            author: data.data.message.author,
            image: data.data.message.image,
            audio: data.data.message.audio,
            richAudio: data.data.message.richAudio,
            bookFile: data.data.message.bookFile,
            description: data.data.message.description,
            category: data.data.message.category,
            price: data.data.message.price,
            language: data.data.message.language,
            isbnNumber: data.data.message.isbnNumber,
          });
          // console.log(data.data.message);
        } else {
          setmessageDetails({
            ...messageDetails,
            message: data.data.message,
            status: "error",
            loader4: false,
          });
          console.log(data.data.message);
        }
      })
      .catch((error) => {
        setfileUpdateDetails({
          ...fileUpdateDetails,
          loader4: false,
        });
        setmessageDetails({
          ...messageDetails,
          message: error.message,
          status: "error",
          loader4: false,
        });
      });
  };
  const handleRouteToRead = () => {
    dispatch({
      type: "book_link",
      data: {
        bookFile: fieldDetails.bookFile,
        bookId: productId,
        title: fieldDetails.title,
      },
    });
    history.push("/read");
  };
  const [showSidebar, setshowSidebar] = useState(false);
  const handleClick = () => {
    if (showSidebar) {
      setshowSidebar(false);
    } else {
      setshowSidebar(true);
    }
  };
  return authExpire ? (
    <div class="card">
      <div class="card-body">
        Your session has expired.
        <Link to="/signin">
          <Button
            className="btn btn-success"
            style={{ marginLeft: "5px", color: "white" }}
          >
            Click to Sign in
          </Button>
        </Link>
      </div>
    </div>
  ) : userData ? (
    <div className="row dashboard__cover m-0">
      <div className="col-md-2 side__bar__cover">
        <SideBar handAndShow={showSidebar} />
      </div>
      <div className="col routerPage__cover">
        <Header />
        <div className="icon__div">
          <div
            style={{
              width: "100%",
              display: "flex",
              justifyContent: "flex-end",
              paddingRight: "20px",
              marginBottom: "10px",
            }}
          >
            <MenuIcon
              style={{ fontSize: "35px", color: "#1941CF", cursor: "pointer" }}
              onClick={handleClick}
            />
          </div>
        </div>
        <div className="dashboard__cover">
          {updateLoader ? (
            <PageLoader />
          ) : (
            <div>
              <div className="publisher__main__cover row m-0">
                <div className="col-12">
                  <label>Title</label>
                  <input
                    type="text"
                    className="published__input"
                    placeholder="Title"
                    value={fieldDetails.title}
                    onChange={(evt) =>
                      handleUpdateDetails(evt.target.value, "title")
                    }
                  />
                  <label>Author</label>
                  <input
                    type="text"
                    className="published__input"
                    placeholder="Title"
                    value={fieldDetails.author}
                    onChange={(evt) =>
                      handleUpdateDetails(evt.target.value, "author")
                    }
                  />
                  <label>Enter ISBN Number</label>
                  <input
                    type="text"
                    className="published__input"
                    placeholder="Title"
                    value={fieldDetails.isbnNumber}
                    onChange={(evt) =>
                      handleUpdateDetails(evt.target.value, "isbnNumber")
                    }
                  />

                  <label>Category</label>
                  <select
                    class="custom-select published__input"
                    id="inputGroupSelect01"
                    value={fieldDetails.category}
                    onChange={(evt) =>
                      handleUpdateDetails(evt.target.value, "category")
                    }
                  >
                    {getCategories.length
                      ? getCategories.map((category) => {
                          return (
                            <option value={category._id}>
                              {category.name}
                            </option>
                          );
                        })
                      : ""}
                  </select>
                  <label>Language</label>
                  <input
                    type="text"
                    className="published__input"
                    placeholder="Title"
                    value={fieldDetails.language}
                    onChange={(evt) =>
                      handleUpdateDetails(evt.target.value, "language")
                    }
                  />
                  <label>Price</label>
                  <input
                    type="text"
                    className="published__input"
                    placeholder="Title"
                    value={fieldDetails.price}
                    onChange={(evt) =>
                      handleUpdateDetails(evt.target.value, "price")
                    }
                  />
                  <label>Description</label>
                  <textarea
                    type="text"
                    className="form-control"
                    placeholder="Description"
                    rows="5"
                    value={fieldDetails.description}
                    onChange={(evt) =>
                      handleUpdateDetails(evt.target.value, "description")
                    }
                  ></textarea>

                  {messageDetails.message ? (
                    <SimpleAlerts details={{ ...messageDetails }} />
                  ) : (
                    ""
                  )}
                  {messageDetails.loader ? (
                    <Button className="save__button">Please wait...</Button>
                  ) : (
                    <Button
                      className="save__button"
                      onClick={handleUpdateButton}
                    >
                      Update
                    </Button>
                  )}
                  <div style={{ width: "100%", paddingTop: "20px" }}>
                    <div style={{ marginBottom: "20px" }}>
                      Update book cover
                    </div>
                    <div
                      style={{
                        width: "100%",
                        position: "relative",
                        display: "flex",
                        justifyContent: "center",
                      }}
                    >
                      <div style={{ cursor: "pointer", position: "relative" }}>
                        <label
                          htmlFor="upload_book_cover"
                          style={{ cursor: "pointer" }}
                        >
                          <img
                            src={UploadImage}
                            alt="image_logo"
                            style={{
                              position: "absolute",
                              left: "40%",
                              top: "25%",
                            }}
                          />
                          {fileUpdateDetails.imageUpload ? (
                            <div
                              style={{ paddingTop: "40px", fontWeight: "700" }}
                            >
                              Image found click to upload
                            </div>
                          ) : (
                            <div>
                              {fieldDetails.image ? (
                                <img
                                  src={fieldDetails.image}
                                  alt="book cover"
                                  style={{ width: "200px", height: "200px" }}
                                />
                              ) : (
                                <div
                                  style={{
                                    paddingTop: "40px",
                                    fontWeight: "700",
                                  }}
                                >
                                  No image cover
                                </div>
                              )}
                            </div>
                          )}
                        </label>
                      </div>
                      <input
                        type="file"
                        id="upload_book_cover"
                        style={{ display: "none" }}
                        onChange={(evt) =>
                          handleImageUpload(evt.target.files[0], "imageUpload")
                        }
                      />
                    </div>
                  </div>
                  {fileUpdateDetails.loader1 ? (
                    <Button className="save__button">Please wait...</Button>
                  ) : (
                    <Button
                      className="save__button"
                      onClick={handleUploadImageButton}
                    >
                      Update book cover
                    </Button>
                  )}

                  {/*PDF FILE upload section */}
                  <div style={{ width: "100%", paddingTop: "20px" }}>
                    <div style={{ marginBottom: "20px" }}>
                      Update an epub file
                    </div>
                    <div
                      style={{
                        width: "100%",
                        position: "relative",
                        display: "flex",
                        justifyContent: "center",
                      }}
                    >
                      <div style={{ cursor: "pointer", position: "relative" }}>
                        <label
                          htmlFor="upload_pdf_cover"
                          style={{ cursor: "pointer" }}
                        >
                          {fileUpdateDetails.bookFileUpload ? (
                            <div
                              style={{ paddingTop: "40px", fontWeight: "700" }}
                            >
                              file found click to upload
                            </div>
                          ) : (
                            <div>
                              {fieldDetails.bookFile ? (
                                <div>
                                  <img src={EpubPng} alt="book cover" /> file
                                  available(Click on icon to change file)
                                </div>
                              ) : (
                                <div
                                  style={{
                                    paddingTop: "40px",
                                    fontWeight: "700",
                                  }}
                                >
                                  <img src={emptyFile} alt="book cover" />
                                  file is empty(Click on icon to upload)
                                </div>
                              )}
                            </div>
                          )}
                        </label>
                      </div>
                      <input
                        type="file"
                        id="upload_pdf_cover"
                        style={{ display: "none" }}
                        onChange={(evt) =>
                          handlePdfoUpload(
                            evt.target.files[0],
                            "bookFileUpload"
                          )
                        }
                      />
                    </div>
                  </div>
                  {fileUpdateDetails.loader4 ? (
                    <Button className="save__button">Please wait...</Button>
                  ) : (
                    <Button
                      className="save__button"
                      onClick={handleUploadPdfButton}
                    >
                      Upload File
                    </Button>
                  )}
                  <div
                    style={{ paddingTop: "50px", width: "100%" }}
                    className="row"
                  >
                    {fieldDetails.audio ? (
                      <div className="col-lg-6 col-md-12 col-12">
                        introductory audio
                        <Player url={fieldDetails.audio} />
                      </div>
                    ) : (
                      ""
                    )}
                    {fieldDetails.richAudio ? (
                      <div className="col-lg-6 col-md-12 col-12">
                        main content audio
                        <Player url={fieldDetails.richAudio} />
                      </div>
                    ) : (
                      ""
                    )}
                  </div>
                  {/* upload audio components */}
                  <div className="col-12">
                    <UploadAudioComponent productId={productId} />
                  </div>

                  <div style={{ marginTop: "50px" }}>
                    <Button
                      style={{
                        background: "#28C76F",
                        color: "white",
                        width: "50%",
                        paddingTop: "10px",
                        paddingBottom: "10px",
                        borderRadius: "26px",
                      }}
                      onClick={handleRouteToRead}
                    >
                      Click to view Book
                    </Button>
                  </div>
                  <div style={{ paddingTop: "40px" }}>
                    <DeleteAlert
                      productId={productId}
                      actualToken={actualToken}
                    />
                  </div>
                </div>
              </div>
            </div>
          )}
          {/* <div style={{ width: "100%" }}>
            <ViewPdf url={fieldDetails.bookFile} />
          </div> */}
        </div>
      </div>
    </div>
  ) : (
    ""
  );
}
